body,
html {
  width: 100vw;
  /*overflow-x: hidden;*/
  margin: 0;
  background: #f3f2f1;
}
.page {
  box-sizing: border-box;
  top: 48px;
  padding: 28px 16px 50px 16px;
  position: absolute;
}

.card-enter {
  transform: translate3d(0, 48px, 0);
  opacity: 0;
}

.card-enter-active {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  transition: transform 300ms cubic-bezier(0.1, 0.9, 0.2, 1),
    opacity 300ms cubic-bezier(0.1, 0.9, 0.2, 1) 0.05s;
}

.page.home.page-enter {
  transform: translate3d(-48px, 0, 0);
  opacity: 0.01;
}

.page.home.page-enter-active {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  transition: transform 300ms cubic-bezier(0.1, 0.9, 0.2, 1),
    opacity 300ms cubic-bezier(0.1, 0.9, 0.2, 1) 0.05s;
}

.page.home.page-exit {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.page.home.page-exit-active {
  transform: translate3d(-48px, 0, 0);
  opacity: 0.01;
  transition: transform 300ms cubic-bezier(0.1, 0.9, 0.2, 1),
    opacity 300ms cubic-bezier(0.1, 0.9, 0.2, 1) 0.05s;
}

.page.details.page-enter {
  transform: translate3d(48px, 0, 0);
  opacity: 0.01;
}

.page.details.page-enter.page-enter-active {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  transition: transform 300ms cubic-bezier(0.1, 0.9, 0.2, 1),
    opacity 300ms cubic-bezier(0.1, 0.9, 0.2, 1) 0.05s;
}

.page.details.page-exit {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.page.details.page-exit-active {
  transform: translate3d(48px, 0, 0);
  opacity: 0.01;
  transition: transform 300ms cubic-bezier(0.1, 0.9, 0.2, 1),
    opacity 300ms cubic-bezier(0.1, 0.9, 0.2, 1) 0.05s;
}

hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #ddd;
}

a:link {
  text-decoration: none !important;
}
